*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  font-size: 10px;
  --bg: #f1f5f8;
  --title: #324d67;
  --accent: #ba5d2c;
  --border: #bcccdc;
  --borderA: rgba(20, 20, 150, 0.15);
  --para: #617d98;
}
body {
  background-color: var(--bg);
}
.products {
  margin: 3rem auto;
  font-size: 1.6rem;
  width: 90%;
  text-transform: capitalize;
}
.products label {
  font-weight: 600;
}
.products input {
  background-color: var(--para);
  border: 2px solid var(--title);
  border-radius: 0.25rem;
}
.title {
  color: var(--title);
  font-size: 3rem;
  text-align: center;
}
.row {
  display: flex;
  align-items: center;
  padding: 1rem 3rem;
  justify-content: space-between;
  color: var(--title);
}
.filters-row,
.sort-row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.row-list {
  padding: 0.5rem 0.5rem;
}
.row-list div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.row-list div label {
  margin-right: 2rem;
}
.row-list input {
  max-width: 5rem;
}
.btn {
  text-align: center;
  margin-top: 2rem;
}

.submit {
  background-color: var(--bg);
  border: 2px solid var(--accent);
  padding: 0.5rem 3rem;
  border-radius: 0.5rem;
  transition: 0.6s;
  font-size: 2rem;
  font-weight: 600;
}
.submit:hover {
  background-color: var(--accent);
  border: 2px solid var(--title);
  color: #eeeeee;
}
.list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding: 2rem;
  font-size: 1.6rem;
  text-align: center;
  text-transform: capitalize;
}
.item {
  border: 2px solid var(--accent);
  color: var(--para);
  border-radius: 1rem;
  line-height: 1.5;
  padding: 1rem 0;
  transition: 0.6s ease-in-out;
}
.item:hover {
  border: 2px solid var(--title);
  color: whitesmoke;
  background-color: var(--accent);
}
.item-name,
.item-company {
  color: var(--title);
}
